.category {
  margin-top: 153px;
  padding-bottom: 100px;
  color: var(--color-light);
  background: url(../img/category-ellipse.png) no-repeat right;
  @media (max-width: 1200px) {
    margin-top: 142px;
  }
  @media (max-width: 550px) {
    background: url(../img/category-ellipse.png) no-repeat right,
    url(../img/category-market-wrap-bg.png) no-repeat center bottom -24% / 100% 60%;
  }
  @media (max-width: 360px) {
    margin-top: 91px;
  }

  &__container {
    margin: 0 auto;
    max-width: 1345px;
    padding-right: 25px;
    @media (max-width: 1200px) {
      padding-left: 25px;
    }
    @media (max-width: 360px) {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__breadcrumbs {
    display: flex;
    @media (max-width: 360px) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  &__breadcrumbs-item {
    position: relative;
    display: flex;
    align-items: center;
    font-size: 20px;
    &--svg {
      & img {
        transform: translateY(5px);
      }
    }
    &:first-child {
      @media (max-width: 360px) {
        display: none;;
      }
    }
    &:not(:last-child) {
      margin-right: 41px;
      &::after {
      content: "";
      position: absolute;
      width: 10px;
      height: 10px;
      right: -28px;
      top: 50%;
      transform: translateY(-50%);
      background-image: url(../img/breadCrumbsVector.svg);
      background-repeat: no-repeat;
      }
    }
  }

  &__breadcrumbs-link {
    // vertical-align: middle;
    & img {
      // position: absolute;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: min-content auto min-content;
    grid-template-rows: min-content 1fr;
    margin: 0 auto;
    max-width: 1312px;
    padding-top: 40px;
    padding-left: 3%;
    @media (max-width: 1200px) {
      grid-template-columns: 45.47826086956522% auto min-content;
      padding-left: 0;
      padding-top: calc(4.25vw - (96px - 8vw));
    }
    @media (max-width: 1150px) {
      grid-template-rows: min-content 1fr auto;
    }
    @media (max-width: 768px) {
      grid-template-columns: 330px auto;
      padding-top: 0;
    }
    @media (max-width: 650px) {
      grid-template-columns: none;
      grid-template-rows: none;
      // justify-items: center;
    }
  }

  &__card {
    position: relative;
    padding-top: 112.4031007751938%;
    width: 516px;
    grid-row: span 2;
    background-image: url(../img/category-card-bg.png);
    background-size: 109%;
    background-position: 61%;
    background-repeat: no-repeat;
    border-radius: 27px;
    overflow: hidden;
    @media (max-width: 1200px) {
      width: 100%;
    }
    @media (max-width: 1150px) {
      align-self: start;
    }
    @media (max-width: 768px) {
      margin-top: 37px;
    }
    @media (max-width: 650px) {
      grid-row: 2;
      margin-bottom: 30px;
    }
    @media (max-width: 360px) {
      justify-self: center;
      // align-self: center;
      margin-top: 0;
      // min-width: 330px;
    }

    &-wrap {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 8%;
      padding-bottom: 23px;
      // @media (max-width: 360px) {
      //   min-width: 330px;
      // }
    }
  }

  &__thumbnails {
    display: flex;
    margin-top: auto;
    gap: 21px;
    @media (max-width: 1200px) {
      gap: 33px;
      padding-left: 10px;
      padding-right: 10px;
    }
    @media (max-width: 768px) {
      gap: 19px;
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  &__item {
    width: 140px;
    height: 130px;
  }

  &__img {
    flex-basis: 68%;
    width: 100%;
    height: 100%;
    border-radius: 19%;
    @media (max-width: 768px) {
      // border-radius: 13px;
    }
  }

  --middle-margin-left: calc(7vw - (144px - 10vw));
    @media (max-width: 1200px) {
      --middle-margin-left: 5.166666666666667vw;
    }
    @media (max-width: 650px) {
      --middle-margin-left: 0;
    }

  --middle-top-max-width: 325px;
    @media (max-width: 1150px) {
      --middle-top-max-width: 425px;
    }
    @media (max-width: 650px) {
      --middle-top-max-width: none;
    }

  &__middle-top {
    position: relative;
    align-self: start;
    max-width: var(--middle-top-max-width);
    margin-top: 59px;
    margin-left: var(--middle-margin-left);
    height: min-content;
    --padding-middle-top-bottom: 62px;
    padding-bottom: var(--padding-middle-top-bottom);
    @media (min-width: 1200px) {
      margin-top: 59px;
    }
    @media (max-width: 1199px) {
      margin-top: 4.916666666666667vw;
    }
    @media (max-width: 768px) {
      margin-top: 26px;
    }
    @media (max-width: 650px) {
    --padding-middle-top-bottom: 35px;
    }
    @media (max-width: 360px) {
      margin-top: 31rem;
      padding-left: 10px;
      padding-right: 10px;
      padding-bottom: 15px;
    }
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: calc(var(--padding-middle-top-bottom) / 2);
      width: 82%;
      height: 1px;
      background-color: var(--color-light-str);
      @media (max-width: 650px) {
        display: none;
      }
    }
  }

  &__middle-bottom {
    grid-column: 2;
    grid-row: 2;
    max-width: var(--middle-top-max-width);
    margin-left: var(--middle-margin-left);
    @media (max-width: 650px) {
      grid-column: auto;
      grid-row: auto;
    }
    @media (max-width: 360px) {
      padding-left: 10px;
      padding-right: 10px;
    }
    & span {
      font-weight: 400;
      color: var(--color-light-str);
    }
  }

  &__caption {
    display: flex;
    align-items: baseline;
    min-width: max-content;
    margin-bottom: 28px;
    @media (max-width: 768px) {
      flex-direction: column;
      margin-bottom: 11px;
    }
    @media (max-width: 650px) {
      flex-direction: row;
    }
    @media (max-width: 450px) {
      flex-direction: column;
    }
    @media (max-width: 360px) {
      margin-bottom: 16px;
    }
  }

  &__title {
    margin-right: 25px;
    font-size: 36rem;
    font-weight: 600;
    @media (max-width: 768px) {
      margin-bottom: 14px;
    }
    @media (max-width: 360px) {
      font-size: 32rem;
    }
  }

  &__sizes {
    color: var(--color-light-str);
    @media (max-width: 360px) {
      font-size: 16rem;
    }
  }

  &__main-text {
    font-size: 18rem;
    font-weight: 200;
    line-height: 26px;
    @media (max-width: 1199px) {
      font-size: 16rem;
    }
    @media (max-width: 768px) {
    }
  }

  &__caption-descr {

    @media (max-width: 650px) {
      & br {
        display: none;
      }
    }

  }

  &__pillow-descr {
    padding-bottom: 31px;
    @media (max-width: 360px) {
      padding-bottom: 18px;
    }
  }

  &__pillowcase-descr {
    padding-bottom: 31px;
    @media (max-width: 768px) {
      padding-bottom: 26px;
    }
    @media (max-width: 360px) {
      padding-bottom: 0;
    }
  }

  & .market {
    &__wrap {
      position: relative;
      grid-row: span 2;
      flex-direction: column;
      align-self: center;
      margin-top: -64px;
      @media (max-width: 1200px) {
        margin-top: -42px;
      }
      @media (max-width: 1150px) {
        grid-area: 3/1/4/3;
        align-self: stretch;
        margin-top: 30px;
      }
      @media (max-width: 1150px) {
        padding-top: 39px;
      }
      @media (max-width: 650px) {
        grid-area: auto;
      }
      @media (max-width: 550px) {
        margin: 0;
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 84px;
        padding-bottom: 84px;
        // &::after {
        //   position: absolute;
        //   content: "";
        //   width: 120%;
        //   height: 100%;
        //   background: url(../img/category-market-wrap-bg.png) no-repeat 50% 50% / cover;
        //   }
        }
        // background: url(../img/category-market-wrap-bg.png) no-repeat 50% 50% / cover;

      @media (max-width: 360px) {
        margin: 0;
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 84px;
        padding-bottom: 84px;
        // background: url(../img/category-market-wrap-bg.png) no-repeat 50% 50% / cover;
      }
    }
    &__links-wrap {
      flex-direction: column;
      @media (max-width: 1150px) {
        flex-direction: row;
        justify-content: space-around;
        width: 100%;
        gap: 20px;
      }
      @media (max-width: 1150px) {
        padding-left: 25px;
        padding-right: 25px;
      }
      @media (max-width: 768px) {
        padding-left: 25px;
        padding-right: 25px;
      }
      @media (max-width: 700px) {
        padding-left: 0;
        padding-right: 0;
      }
      @media (max-width: 550px) {
        flex-direction: column;
      }
    }

    &__order {
      margin-right: 0;
      margin-bottom: 38px;
      font-size: 20rem;
      font-weight: 500;
      @media (max-width: 768px) {
        margin-bottom: 18px;
        font-size: 18rem;
      }
      @media (max-width: 360px) {
        margin-bottom: 37px;
      }
    }

    &__link {
      width: 210px;
      padding: 1em 2.5em;
      font-size: 20rem;
      font-weight: 500;
      @media (max-width: 1150px) {
        flex-grow: 1;
      }
      @media (max-width: 768px) {
        flex-basis: 0;
        padding: 1em 1em;
        font-size: 18rem;
      }
      @media (max-width: 550px) {
        width: 100%;
      }
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 21px;
        @media (max-width: 1150px) {
          width: 25%;
          margin-bottom: 0;
        }
        @media (max-width: 550px) {
          width: 100%;
        }
        @media (max-width: 360px) {
          margin-bottom: 10px;
        }
      }
    }
  }

  &__link-care.market__link {
    width: 93%;
    font-size: 16rem;
    font-weight: 600;
    white-space: nowrap;
    @media (max-width: 768px) {
      font-size: 14rem;
    }
    @media (max-width: 767px) {
      width: 100%;
    }
    @media (max-width: 650px) {
      width: 90%;
      margin-left: auto;
      margin-right: auto;
    }
    @media (max-width: 360px) {
      display: none;
    }
  }

}



