.footer {
  position: relative;
  padding-bottom: 133px;
  color: var(--color-light);
  background: url(../img/footer-bg-1440.png) bottom / contain no-repeat;
  @media (max-width:1200px) {
    margin-top: -27px;
    background: url(../img/footer-bg-1200.png) bottom / contain no-repeat;
  }
  @media (max-width:768px) {
    margin-top: -24px;
    background: url(../img/footer-bg-768.png) bottom / contain no-repeat;
  }
  @media (max-width:767px) {
    padding-bottom: 114px;
    background: url(../img/footer-bg-360-top.png) center 20% / contain no-repeat,
                url(../img/footer-bg-360-bottom.png) bottom / contain no-repeat,
                url(../img/footer-bg-360-center.png) 50% 10% / cover no-repeat;
  }
  @media (max-width:360px) {
    padding-bottom: 114px;
    background: url(../img/footer-bg-360-top.png) center 20% / contain no-repeat,
                url(../img/footer-bg-360-bottom.png) bottom / contain no-repeat,
                url(../img/footer-bg-360-center.png) center / cover no-repeat;
  }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 90%;
    height: 1px;
    background: url(../img/footer-line.svg) no-repeat;
    background-size: contain;
  }

  &__container {
    margin: 0 auto;
    max-width: 1440px;
    padding-top: 78px;
    padding-left: 22px;
    padding-right: 22px;
    @media (max-width:768px) {
      padding-right: 22px;
    }
    @media (max-width:750px) {
      padding-top: 38px;
      padding-left: 30px;
      padding-right: 30px;
    }
    @media (max-width:360px) {
      padding-top: 38px;
      padding-left: 30px;
      padding-right: 30px;
    }
  }


  &__inner {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 1347px;
    @media (max-width:1190px) {
      grid-template-columns: auto auto;
    }
    @media (max-width:750px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    @media (max-width:360px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__left {
    display: inline-grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 55px;
    max-width: 478px;
    min-width: 400px;
    @media (max-width:930px) {
      justify-items: start;
    }
    @media (max-width:768px) {
      justify-items: start;
    }
    @media (max-width:750px) {
      min-width: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    @media (max-width:360px) {
      min-width: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__logo-wrap {
    grid-column: span 2;
    display: flex;
    align-items: center;
  }

  &__logo-sleep {
    align-self: center;
    margin-right: 40px;
    @media (max-width:500px) {
      width: 136px;
      margin-right: 43px;
    }
    @media (max-width:360px) {
      width: 136px;
      margin-right: 43px;
    }
  }

  &__logo-baby {
    @media (max-width:768px) {
      justify-self: center;
    }
    @media (max-width:500px) {
      width: 127px;
    }
    @media (max-width:360px) {
      width: 127px;
    }
  }

  &__caption {
    grid-column: span 2;
    margin-top: 20px;
    font-size: 18rem;
    @media (max-width:930px) {
      font-size: 16rem;
    }
    @media (max-width:768px) {
      text-align: center;
      font-size: 16rem;
    }
    @media (max-width:360px) {
      font-size: 12rem;
      margin-top: 28px;
    }
  }

  &__links {
    display: flex;
    justify-content: space-between;
    grid-column: span 2;
    margin-top: 37px;
    padding-right: 11px;
    @media (max-width:768px) {
      margin-top: -9px;
      padding-right: 7px;
    }
    @media (max-width:750px) {
      flex-direction: column;
      align-items: center;
      margin-top: 35px;
      margin-bottom: 22px;
      padding-right: 0;
    }
    @media (max-width:360px) {
      flex-direction: column;
      align-items: center;
      margin-top: 35px;
      margin-bottom: 22px;
      padding-right: 0;
    }
  }

  &__link {
    color: inherit;
    text-decoration: underline;

    @media (max-width:930px) {
      font-size: 14rem;
      @include mr (40px);
    }
    @media (max-width:768px) {
      font-size: 14rem;
      @include mr (40px);
    }
    @media (max-width:750px) {
      @include mr (0);
      margin-bottom: 27px;
      &:first-child {
        order: 3;
      }
    }
    @media (max-width:360px) {
      @include mr (0);
      margin-bottom: 27px;
      &:first-child {
        order: 3;
      }
    }
  }

  &__right {
    align-self: start;
    padding-top: 6px;
    @media (max-width:1190px) {
      flex-direction: column;
      justify-self: end;
    }
    @media (max-width:1090px) {
      justify-self: center;
      width: 100%;
    }
    @media (max-width:992px) {
      justify-self: end;
      padding-right: 28px;
    }
    @media (max-width:768px) {
      flex-direction: column;
      justify-self: end;
      padding-right: 14px;
    }
    @media (max-width:750px) {
      width: 100%;
      flex-direction: column;
      align-items: center;
      align-self: auto;
      padding-top: 0;
      padding-right: 0;
    }
    @media (max-width:360px) {
      width: 100%;
      flex-direction: column;
      align-items: center;
      align-self: auto;
      padding-top: 0;
      padding-right: 0;
    }
  }

  & .market__links-wrap {
    gap: 12px;
    @media (max-width:1090px) {
      flex-direction: column;
      width: 100%;
    }
    @media (max-width:768px) {
      flex-direction: column;
    }
    @media (max-width:750px) {
      width:100%;
      align-items: center;
    }
    @media (max-width:360px) {
      width:100%;
      align-items: center;
    }
  }

  & .market__order {
    margin-right: 11px;
    font-size: 16px;
    @media (max-width:1190px) {
      margin-right: 0;
      margin-bottom: 15px;
    }
    @media (max-width:768px) {
      margin-right: 0;
      margin-bottom: 15px;
    }
    @media (max-width:750px) {
      font-size: 20px;
      margin-bottom: 36px;
    }
    @media (max-width:360px) {
      font-size: 20px;
      margin-bottom: 36px;
    }
  }

  & .market__link {
    width: 180px;
    @include mr (0);
    @media (max-width:1190px) {
      padding: 0.8em 2.5em;
    }
    @media (max-width:1190px) {
      width: 100%;
    }
    @media (max-width:768px) {
      padding: 0.8em 2.5em;
    }
    @media (max-width:750px) {
      width: 100%;
      @include mb (21px);
      font-size: 20px;
    }
    @media (max-width:360px) {
      width: 100%;
      @include mb (21px);
      font-size: 20px;
    }
  }

  &__copyright {
    margin-top: 41px;
    font-size: 16rem;
    color: var(--color-light-str);
    & br {
      display: none;
      @media (max-width:360px) {
        display: block;
      }
    }
    @media (max-width:768px) {
      font-size: 14px;
      margin-top: -15px;
    }
    @media (max-width:750px) {
      text-align:center;
      margin-top: 36px;
      // font-size: 12px;
    }
    @media (max-width:360px) {
      text-align:center;
      margin-top: 36px;
      font-size: 12px;
    }
  }

  &__design {
    position: relative;
    justify-self: end;
    align-self: end;
    padding-right: 174px;
    line-height: 1.5;
    color: var(--color-light-str);
    @media (max-width:768px) {
      grid-column: span 2;
      grid-row: 3;
      justify-self: center;
      margin-top: 99px;
      padding-right: 160px;
    }
    @media (max-width:750px) {
      margin-top: 56px;
      // font-size: 12rem;
      align-self: auto;
      // padding-right: 118px;
    }
    @media (max-width:360px) {
      margin-top: 56px;
      font-size: 12rem;
      align-self: auto;
      padding-right: 118px;
    }
    &::after {
      content: "";
      position: absolute;
      display: block;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 141px;
      height: 41px;
      background-image: url(../img/footer-designer.svg);
      background-repeat: no-repeat;
      background-size: 100%;
      // @media (max-width:750px) {
      //   top: 85%;
      //   width: 101px;
      // }
      @media (max-width:360px) {
        top: 85%;
        width: 101px;
      }
    }
  }
}


.div {
  display: grid;
  // grid-template-columns: auto;
  margin: 200px;

  & .item {
    height: 100px;
    width: 100px;
    border: 1px solid black;
    background-color: green;
  }
}

