.carousel {
  color: var(--color-light);

  &__container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 4.513888888888889%;
    @media (max-width: 1260px) {
      padding: 0 25px;
    }
    @media (max-width: 1200px) {
      padding: 0 25px;
    }
  }

  &__carousel-wrap {
    position: relative;
    margin: 0 -25px;
    margin-bottom: 77px;
    @media (max-width: 768px) {
      margin-bottom: 48px;
    }
  }

  &__slides-wrap {
    display: flex;
  }

  &__slide {
    width: auto;
    // @include mr (-10px);
    width: 20%;
    @media (max-width: 1200px) {
      width: 241.5px;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    // width: 268px;
    // width: 20%;

    &-wrap-img {
      position: relative;
      width: 100%;
      padding-bottom: 68.6%;
      margin-bottom: 15px;
      margin-bottom: -7px;
      @media (max-width: 1200px) {
        margin-bottom: 15px;
      }
      @media (max-width: 360px) {
        margin-bottom: 26px;
      }
    }

    &-img {
      position: absolute;
      top: 0;
      left: 0;
      // object-fit: contain;
      // object-position: 50% 50%;
      width: 100%;
      // height: 200px;
    }

    &-caption {
      font-size: 24rem;
      margin-bottom: 7px;
      @media (max-width: 360px) {
        font-size: 20rem;
      }
    }
    &-descr {
      font-size: 16rem;
      color: var(--color-light-str);
      @media (max-width: 360px) {
        font-size: 14rem;
      }
    }
  }

  & .swiper-button-prev, .swiper-button-next {
    z-index: 2;
      top: 50%;
      transform: translateY(-50%);
      bottom: auto;
      width: 30px;
      height: 30px;
      border-radius: 100%;
      background-color: rgba(255, 255, 255, 0.2);
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: 20%;
      box-shadow: 0 0 10px 10px rgba(255, 255, 255, 0.2);
      -webkit-tap-highlight-color: transparent;
  }
  & .swiper-button-prev {
    left: -45px;
    @media (max-width: 1490px) {
      left: -20px;
    }
  }
  & .swiper-button-next {
    right: -45px;
    @media (max-width: 1490px) {
      right: -20px;
    }
  }
}
