.market {
  &__wrap {
    display: flex;
    align-items: center;
    color: var(--color-light);
  }


  &__order {
    margin-right: 22px;
    white-space: nowrap;
    font-size: 18px;
    color: rgba(255, 255, 255, .5);
    @media (max-width:1200px) {
      font-size: 16px;
    }
    @media (max-width:992px) {
      margin: 0;
      margin-bottom: 10px;
      font-size: 15px;
    }
    @media (max-width:768px) {
      font-size: 14px;
    }
  }

  &__links-wrap {
    display: flex;
  }

  &__link {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 0;
      width: 194px;
      @include mr (2vw);
      padding: .7em 2.5em;
      border-radius: 1.5em;
      font-size: 18px;
      font-weight: 500;
      background-color: rgba(255, 255, 255, .2);
      color:inherit;
      &--oz {
        width: 146px;
      }
      @media (max-width:1200px) {
        width: 180px;
        @include mr (1vw);
      }
      @media (max-width:870px) {
        width: 170px;
        font-size: 17px;
      }
      @media (max-width:768px) {
        width: 160px;
        font-size: 16px;
      }
    }
}

