.benefits {
  margin-top: 72px;
  color: var(--color-light);
  background-image: url(../img/benefits-bg-1440.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  @media (max-width: 1200px) {
    margin-top: 11.33333333333333vw;
  }
  @media (max-width: 1100px) {
    background-image: url(../img/benefits-bg-768.png);
  }
  @media (max-width: 768px) {
    margin-top: 66px;
    background-image: url(../img/benefits-bg-768.png);
    background-size: cover;
  }
  @media (max-width: 550px) {
    background-image: url(../img/benefits-bg-360.png);
  }
  @media (max-width: 360px) {
    margin-top: 59px;
    background-image: url(../img/benefits-bg-360.png);
  }

  &__container {
    padding: 10px 25px;
    @media (max-width: 1200px) {
      padding: 10px 60px;
    }
    @media (max-width: 1100px) {
      padding: 36px 60px;
    }
    @media (max-width: 768px) {
      padding: 36px 60px;
      padding-bottom: 5px;
    }
    @media (max-width: 550px) {
      padding: 36px 10px;
    }
  }

  &__items {
    display: grid;
    // grid-template-columns: 50% 50%;
    grid-template-columns: repeat(2, minmax(auto, 504px));
    grid-template-rows: auto auto;
    grid-auto-flow: column;
    justify-content: space-between;
    // column-gap: 4.824561403508772%;
    row-gap: 5px;
    max-width: 1140px;
    margin: 0 auto;
    @media (max-width: 768px) {
      grid-template-columns: repeat(1, minmax(auto, 504px));
      grid-template-rows: repeat(4, 166px);
      grid-auto-flow: row;
      row-gap: 40px;
      justify-content: center;
    }
    @media (max-width: 1100px) {
      grid-template-columns: repeat(1, minmax(auto, 504px));
      grid-template-rows: repeat(4, minmax(166px, auto));
      grid-auto-flow: row;
      row-gap: 40px;
      justify-content: center;
    }
    @media (max-width: 550px) {
      grid-template-columns: repeat(1, minmax(auto, 504px));
      grid-template-rows: repeat(4, minmax(181px, auto));
      row-gap: 27px;
    }
    @media (max-width: 360px) {
      grid-template-columns: repeat(1, minmax(auto, 504px));
      grid-template-rows: repeat(4, minmax(181px, auto));
      row-gap: 27px;
    }
  }

  &__item {
    position: relative;
    // width: 50%;
    padding: 40px 50px;
    padding-left: 158px;
    padding-right: 10px;
    border-radius: 20px;
    @media (max-width: 550px) {
      padding: 31px 10px;
      padding-left: 138px;
      // padding-right: 0;
      background-color: rgba(255, 255, 255, 0.05);
    }
    @media (max-width: 360px) {
      padding: 31px 10px;
      padding-left: 138px;
      // padding-right: 0;
      background-color: rgba(255, 255, 255, 0.05);
    }
    &::before {
      content: "";
      position: absolute;
      top: 55%;
      left: -64px;
      transform: translateY(-50%);
      width: 304px;
      height: 247px;
      // background-image: url(../img/benefits-1.png);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      @media (max-width: 1200px) {
        left: -44px;
      }
      @media (max-width: 550px) {
        top: 50%;
        left: -87px;
      }
      @media (max-width: 360px) {
        top: 50%;
        left: -87px;
      }
    }

    &-1::before {
      // top: 55%;
      background-image: url(../img/benefits-1.png);
    }
    &-2 {
      // grid-row: 2;
      &::before {
        // top: 42%;
        background-image: url(../img/benefits-2.png);
        background-position: 7px -22px;
      }
    }
    &-3 {
      justify-self:end;
      &::before {
        // top: 47%;
        background-image: url(../img/benefits-3.png);
        background-position: -13px -13px;
      }
    }
    &-4 {
      &::before {
        // top: 50%;
        background-image: url(../img/benefits-4.png);
        background-position: -13px -13px;
      }
    }
  }

  &__caption {
    margin-bottom: 20px;
    font-size: 22rem;
    font-weight: 400;
    @media (max-width: 550px) {
      font-size: 18rem;
    }
    @media (max-width: 360px) {
      font-size: 18rem;
    }
  }

  &__descr {
    font-size: 18rem;
    font-weight: 300;
    @media (max-width: 550px) {
      font-size: 14rem;
      font-weight: 200;
    }
    @media (max-width: 360px) {
      font-size: 14rem;
      font-weight: 200;
    }
  }
}
