.product-carousel {
  margin-top: 16px;
  margin-bottom: 90px;
  color: var(--color-light);
  text-align: center;
  @media (max-width: 1200px) {
    margin-top: -29px;
    margin-bottom: 133px;
  }
  @media (max-width: 768px) {
    margin-top: -39px;
  }
  @media (max-width: 360px) {
    margin-top: -96px;
    margin-bottom: 105px;
  }

  &__title {
    margin-bottom: 30px;
    font-size: 22rem;
    font-weight: 600;
    @media (max-width: 1200px) {
      margin-bottom: 50px;
    }
    @media (max-width: 768px) {
      font-size: 20rem;
    }
    @media (max-width: 450px) {
      font-weight: 500;
      & span {
        display: none;
      }
    }
    @media (max-width: 360px) {
      font-weight: 500;
      & span {
        display: none;
      }
    }
  }
}
