.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  padding-bottom: 152px;
  color: var(--color-light);
  @media (min-width: 1440px) {
    background: url(../img/header-ellipse.png) no-repeat,
                url(../img/header-ellipse-big.png) no-repeat right top;
  };
  background: url(../img/header-ellipse.png) no-repeat,
              url(../img/header-ellipse-big.png) no-repeat;


  &__container {
    // position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 3%;
    padding-top: 32px;
    // background-color: var(--color-azure);
    @media (max-width:1300px) {
      padding: 0 20px;
      padding-top: 32px;
    }
    @media (max-width:1200px) {
      padding: 0 20px;
      padding-top: clamp(23px, 3vw, 32px);
      // padding-top: max(23px, 2.666666666666667vw);
    }
    @media (max-width:992px) {
      &:not(.mob-menu) {
        align-items: flex-end;
      }
    }
    @media (max-width:768px) {
      padding-top: 23px;
    }
  }

  &__container.mob-menu {
    display: none;
    position: relative;
    @media (max-width:1150px) {
      position: fixed;
      display: flex;
      flex-direction: column;
      width: 287px;
      top: 0;
      right: 0;
      padding-top: 10px;
      padding-bottom: 23px;
      color: var(--color-azure);
      background-color: var(--color-light);
      transform: translateX(100%);
      transition: transform 0.3s ease-in-out;
      &.menu--active {
        // right: 0%;
        transform: none;
      }
      & .header__left {
        display: flex;
        flex-direction: column;
        margin-bottom: 214px;
      }
      & .header__logo {
        margin: 0;
        margin-bottom: 44px;
        padding: 0;
        @media (max-width:500px) {
          font-size: 43px;
        }
        & span {
          @media (max-width:720px) {
            display: flex;
          }
        }
      }
      & .header__nav {
        display: block;
      }
      & .header__list {
        flex-direction: column;
        align-items: center;

      }
      & .header__item {
        @include mr (0);
        @include mb (70px);
      }
      & .header__right {
        display: flex;
        flex-direction: column;
      }
      & .header__order {
        margin: 0;
        margin-bottom: 16px;
        color: var(--color-azure);
      }
      & .header__market-wrap {
        flex-direction: column;
      }
      & .header__market-link {
        width: 220px;
        margin: 0;
        @include mb (13px);
        padding: 0.8em 2.5em;
        font-size: 20px;
        color: var(--color-light);
        background-color: var(--color-azure-2);
      }
    }
  }

  &__left {
    display: flex;
    align-items: center;
  }

  &__logo {
    margin-right: 5vw;
    padding-top: 3%;
    @media (max-width:1300px) {
      margin-right: 4vw;
    }
    @media (max-width:1150px) {
      margin-right: 0;
    }
    @media (max-width:768px) {
      padding-top: 0;
    }
    @media (max-width:500px) {
      font-size: 36px;
    }
    & span {
      margin-top: 3px;
      font-family: "ruberoid";
      font-size: 12px;
      @media (max-width:720px) {
        display: none;
      }
    }
  }

  &__nav {
    @media (max-width:1150px) {
      display: none;
    }
  }

  &__list {
    display: flex;
  }

  &__item {
    @include mr (3vw);
  }

  &__link {
    font-size: 20px;
    color: inherit;
  }

  &__right {
    display: flex;
    flex-basis: 54.5%;
    align-items: center;
    @media (max-width:992px) {
      flex-direction: column;
    }
    @media (max-width:720px) {
      display: none;
    }
  }

  &__order {
    margin-right: 22px;
    white-space: nowrap;
    font-size: 18px;
    color: rgba(255, 255, 255, .5);
    @media (max-width:1200px) {
      font-size: 16px;
    }
    @media (max-width:992px) {
      margin: 0;
      margin-bottom: 10px;
      font-size: 15px;
    }
    @media (max-width:768px) {
      font-size: 14px;
    }
  }

  &__market {
    &-wrap {
      display: flex;
    }

    &-link {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 0;
      width: 194px;
      @include mr (2vw);
      padding: .7em 2.5em;
      border-radius: 1.5em;
      font-size: 18px;
      font-weight: 500;
      background-color: rgba(255, 255, 255, .2);
      color:inherit;
      &--oz {
        width: 146px;
      }
      @media (max-width:1200px) {
        width: 180px;
        @include mr (1vw);
      }
      @media (max-width:870px) {
        width: 170px;
        font-size: 17px;
      }
      @media (max-width:768px) {
        width: 160px;
        font-size: 16px;
      }
    }
  }

  &__burger {
    display: none;
    --burger-width: 38px;
    --burger-height: 28px;
    --burger-line-height: 3px;
    @media (max-width:1150px) {
      position: relative;
      // z-index: 1;
      display: block;
      color: var(--color-light);
    }
    @media (max-width:992px) {
      margin-bottom: 6px;
      margin-right: 7px;
    }
    @media (max-width:768px) {
      margin-bottom: 6px;
      margin-right: 7px;
    }
    @media (max-width:500x) {
      margin-right: 10px;
    }
    @media (max-width:360px) {
      margin-right: 10px;
    }
    &--close {
      display: block;
      position: absolute;
      top: 20px;
      right: 20px;
      margin: 0;
      color: var(--color-azure);
    }
  }
}



