.hero {
  margin-top: 111px;
  color: var(--color-light);
  background-image: url('../img/hero-bg.png');
  // background-position: left 50% bottom 70%;
  background-position: left 50% bottom -340px;

  // background-position: right 50% top -101px;
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-size: cover;
  // background-size: contain;
    @media (min-width: 1441px) {
      background-image: url('../img/hero-bg-1200.png');
      background-position-x: 50%;
      background-position-y: 81%;
    }
    @media (max-width: 1290px) {
      background-image: url('../img/hero-bg-1200.png');
      background-position-x: 50%;
      background-position-y: 81%;
    }
    @media (max-width: 1200px) {
      // background-position: left 50% bottom -60px;
      // background-position-y: 61%;
      background-position: center bottom -43px;
    }
    @media (max-width: 930px) {
      // background-position-y: 40%;
    }
    @media (max-width: 860px) {
      // background-position-y: 40%;
    }
    @media (max-width: 768px) {
      margin-top: 89px;
      background-image: url('../img/hero-bg-768.png');
      background-position-x: 50%;
      background-position-y: 26%;
    }
    @media (max-width: 700px) {
      background-position-y: 16%;
    }
    @media (max-width: 720px) {
      margin-top: 64px;
    }
    @media (max-width: 630px) {
      background-position-y: 0%;
    }
    @media (max-width: 595px) {
      background-image: none;
    }
    @media (max-width: 360px) {
      margin-top: 64px;
    }

  &__container {
    padding-top: 25px;
    padding-bottom: min(230px, 16%);
    padding-left: 17px;
    padding-right: 17px;
    @media (max-width: 1290px) {
      padding-bottom: 14%;
    }
    @media (max-width: 1200px) {
      padding-top: 2.083333333333333%;
      padding-bottom: 14%;
    }
    @media (max-width: 768px) {
      padding-top: 5px;
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 15%;
    }
  }

  &__swiper-container {
    position: relative;
    max-width: 1222px;
    margin: 0 auto;
  }

  &__swiper {
    min-width: 100%;
    padding-top: 148px;
    @media (max-width: 835px) {
    padding-top: 126px;
    }
    @media (max-width: 768px) {
    padding-top: 126px;
    }
    @media (max-width: 734px) {
    padding-top: 25px;
    }
  }

  &__slide-container {
    display: flex;
    height: 286px;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    background-color: rgba(255, 255, 255, 0.2);
    @media (max-width: 992px) {
      height: 264.4463768115942px;
    }
    @media (max-width: 835px) {
      height: 242.8927536231884px;
    }
    @media (max-width: 768px) {
      height: 242.8927536231884px;
    }
    @media (max-width: 734px) {
      align-items: center;
      height: 347px;
      border-radius: 15px;
      background-image: url('../img/girl-bg.png');
      background-repeat: no-repeat;
      background-position: 100% 0%;
    }
  }

  &__content {
    flex-grow: 1;
    padding-top: 40px;
    padding-left: 48px;
    padding-right: 6%;
    padding-bottom: 34px;
    // background-color: rgba(255, 255, 255, 0.2);
    @media (max-width: 1260px) {
      padding-left: 43px;
      padding-right: 22px;
    }
    @media (max-width: 1200px) {
      padding-top: max(28px, 3.430531732418525%);
      padding-left: 3.687821612349914%;
      padding-right: 22px;
      padding-bottom: 34px;
    }
    @media (max-width: 768px) {
      padding-top: 28px;
      padding-left: 21px;
      padding-bottom: 27px;
    }
    @media (max-width: 360px) {
      padding-top: 17px;
      padding-left: 32px;
      padding-right: 16px;


    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-family: 'magnolia-script';
    font-size: 48px;
    font-weight: 200;
    letter-spacing: 0.01em;
    @media (max-width: 1053px) {
      font-size: 40px;
      margin-bottom: 24px;
      letter-spacing: 0.02em;
    }
    @media (max-width: 932px) {
      font-size: 37px;
    }
    @media (max-width: 890px) {
      font-size: 33px;
    }
    @media (max-width: 835px) {
      font-size: 27px;
      letter-spacing: 0.03em;
    }
    @media (max-width: 734px) {
      font-size: 34px;
      letter-spacing: 0.03em;
    }
    @media (max-width: 734px) {
      margin-bottom: 40px;
      letter-spacing: 0.01em;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 50px;
    font-size: 17px;
    line-height: 25px;
    @media (max-width: 1199px) {
      margin-bottom: 42px;
      font-size: 16px;
    }
    @media (max-width: 1053px) {
      font-size: 16px;
    }
    @media (max-width: 992px) {
      font-size: 15px;
    }
    @media (max-width: 835px) {
      margin-bottom: 42px;
      font-size: 14px;
      line-height: 21px;
    }
    @media (max-width: 768px) {
      margin-bottom: 42px;
      font-size: 14px;
      line-height: 21px;
    }
    @media (max-width: 734px) {
      margin-bottom: 25px;
      font-size: 15px;
      line-height: 25px;
    }
    @media (max-width: 360px) {
      line-height: 22px;
    }
  }

  &__slogan {
    margin: 0;
    font-family: 'magnolia-script';
    font-size: 32px;
    @media (max-width: 1053px) {
      font-size: 28px;
    }
    @media (max-width: 890px) {
      font-size: 26px;
    }
    @media (max-width: 835px) {
      font-size: 24px;
    }
    @media (max-width: 768px) {
      font-size: 24px;
    }
    @media (max-width: 734px) {
      font-size: 22px;
    }
    & span {
      margin-left: 16px;
      font-family: "Ruberoid";
      font-size: 22px;
      color: var(--color-light-str);
      @media (max-width: 1200px) {
        margin-left: 27px;
        font-size: 18px;
      }
      @media (max-width: 890px) {
        font-size: 17px;
      }
      @media (max-width: 835px) {
        margin-left: 12px;
        font-size: 16px;
      }
      @media (max-width: 768px) {
        margin-left: 12px;
        font-size: 16px;
      }
      @media (max-width: 734px) {
        margin-left: 10px;
        font-size: 14px;
      }
    }
  }
// 345(100%)*286(82,89855072463768%)
  &__img {
    position: relative;
    flex-shrink: 0;
    width: 345px;
    @media (max-width: 992px) {
      width: 319px;
    }
    @media (max-width: 835px) {
      width: 293px;
    }
    @media (max-width: 768px) {
      width: 293px;
    }
    @media (max-width: 734px) {
      display: none;
    }
    & img {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
    }
  }
}

.swiper-button-prev, .swiper-button-next {
  position: absolute;
  top: auto;
  bottom: 136px;
  width: 16px;
  height: 32px;
  @media (max-width: 1316px) {
    display: none;
  }
  &::after {
    content: "";
    width: 100%;
    height: 100%;
  }
}

.swiper-button-prev {
  left: -5%;
  background-image: url("../img/arrow-left.svg");

  @media (max-width: 1375px) {
    left: -3%;
  }
  // &::after {
  // }
}
.swiper-button-next {
  right: -5%;
  background-image: url("../img/arrow-right.svg");
  @media (max-width: 1375px) {
    right: -3%;
  }
}

.swiper-horizontal > .swiper-pagination-bullets,
 .swiper-pagination-bullets.swiper-pagination-horizontal,
  .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: -48px;
  display: none;
  justify-content: center;
  gap: 12px;
  @media (max-width: 1316px) {
    display: flex;
  }


  & .swiper-pagination-bullet {
    width: 22px;
    height: 22px;
    margin-right: 2px;
    background-color: rgba(255, 255, 255, 0.5);
  }
}


