.catalog {
  margin-top: 48px;
  color: var(--color-light);
  background-image: url(../img/cat-baby-bg.png);
  background-repeat: no-repeat;
  background-position: 0% 63%;
  @media (max-width: 1200px) {
    margin-top: 72px;
  }
  @media (max-width: 815px) {
    background-position: 0% 59%;
  }
  @media (max-width: 768px) {
    margin-top: 52px;
    background-position: -11% 56%;
  }
  @media (max-width: 750px) {
    background-position: -11% 33%;
  }
  @media (max-width: 500px) {
    background-position: -63px 34%;
  }
  @media (max-width: 360px) {
    margin-top: 65px;
  }

  &__container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 4.513888888888889%;
    @media (max-width: 1260px) {
      padding: 0 25px;
    }
    @media (max-width: 1200px) {
      padding: 0 25px;
      // padding: 0 0;
    }
  }

  &__caption {
    display: flex;
    align-items: baseline;
    margin-left: 46px;
    margin-bottom: 69px;
    @media (max-width: 1200px) {
      margin-left: 0;
    }
    @media (max-width: 815px) {
      align-items: center;
      margin-bottom: 37px;
    }
    @media (max-width: 768px) {
      align-items: center;
      margin-bottom: 4.817708333333333vw;
    }
    @media (max-width: 600px) {
      display: block;
      margin-bottom: 16px;
    }
    @media (max-width: 360px) {
      display: block;
      margin-bottom: 16px;
    }
    & br {
      display: none;
      @media (max-width: 815px) {
        display: block;
      }
      @media (max-width: 768px) {
        display: block;
      }
    }
  }

  &__logo {
    margin-right: 2%;
    font-family: "magnolia-script";
    font-size: 42rem;
    @media (max-width: 1200px) {
      font-size: 40rem;
    }
    @media (max-width: 768px) {
      letter-spacing: 1rem;
    }
    @media (max-width: 360px) {
      margin-bottom: 18px;
    }
  }

  &__title {
    font-size: 20rem;
    color: var(--color-light-str);
    @media (max-width: 1200px) {
      font-size: 16rem;
    }
    @media (max-width: 815px) {
      margin-top: 6px;
      line-height: 135%;
    }
    @media (max-width: 500px) {
      font-size: 14rem;
    }
    @media (max-width: 360px) {
      font-size: 14rem;
    }
  }

  &__carousel-wrap {
    position: relative;
    margin: 0 -25px;
    margin-bottom: 77px;
    @media (max-width: 768px) {
      margin-bottom: 48px;
    }
  }

  &__slides-wrap {
    display: flex;
  }

  &__slide {
    width: auto;
    // @include mr (-10px);
    width: 20%;
    @media (max-width: 1200px) {
      width: 241.5px;
    }
  }

  &__card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    // width: 268px;
    // width: 20%;

    &-wrap-img {
      position: relative;
      width: 100%;
      padding-bottom: 68.6%;
      margin-bottom: 15px;
      margin-bottom: -7px;
      @media (max-width: 1200px) {
        margin-bottom: 15px;
      }
      @media (max-width: 360px) {
        margin-bottom: 26px;
      }
    }

    &-img {
      position: absolute;
      top: 0;
      left: 0;
      // object-fit: contain;
      // object-position: 50% 50%;
      width: 100%;
      // height: 200px;
    }

    &-caption {
      font-size: 24rem;
      margin-bottom: 7px;
      @media (max-width: 360px) {
        font-size: 20rem;
      }
    }
    &-descr {
      font-size: 16rem;
      color: var(--color-light-str);
      @media (max-width: 360px) {
        font-size: 14rem;
      }
    }

    &-link {
      position: absolute;
      width: 100%;
      height: 100%;
    }
  }

  & .market__wrap {
    margin-left: 3.5%;
    @media (max-width: 1330px) {
      margin-left: 0;
    }
    @media (max-width: 1200px) {
      margin-left: 0;
    }
    @media (max-width: 1190px) {
      justify-content: center;
    }
    @media (max-width: 1070px) {
      flex-direction: column;
    }
    @media (max-width: 768px) {
      flex-direction: column;
    }
    @media (max-width: 360px) {
      flex-direction: column;
    }
  }

  & .market__order {
    position: relative;
    font-size: 20px;
    padding-right: 88px;
    @media (max-width: 1070px) {
      padding-right: 0;
      margin-bottom: 25px;
    }
    @media (max-width: 768px) {
      padding-right: 0;
      margin-bottom: 25px;
    }
    &::after {
      position: absolute;
      content: "";
      display: block;
      right: 10px;
      top: 50%;
      background-image: url('../img/order-arrow.svg');
      background-repeat: no-repeat;
      width: 60px;
      height: 23px;
      @media (max-width: 1070px) {
        display: none;
      }
      @media (max-width: 768px) {
        display: none;
      }
    }
  }

  & .market__link {
    width: 284px;
    padding: 1.2em 2.5em;
    @include mr (40px);
    @media (max-width: 1400px) {
      width: 270px;
    }
    @media (max-width: 1260px) {
      @include mr (23px);
    }
    @media (max-width: 1200px) {
      @include mr (23px);
    }
    @media (max-width: 1190px) {
      width: 220px;
      font-size: 20rem;
    }
    @media (max-width: 768px) {
      width: 220px;
      font-size: 20rem;
    }
    @media (max-width: 750px) {
      @include mr (0);
    }
    @media (max-width: 360px) {
      @include mr (0);
    }
  }

  & .swiper-button-prev, .swiper-button-next {
    z-index: 2;
      top: 50%;
      transform: translateY(-50%);
      bottom: auto;
      width: 30px;
      height: 30px;
      border-radius: 100%;
      background-color: rgba(255, 255, 255, 0.2);
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: 20%;
      box-shadow: 0 0 10px 10px rgba(255, 255, 255, 0.2);
      -webkit-tap-highlight-color: transparent;
  }
  & .swiper-button-prev {
    left: -45px;
    @media (max-width: 1490px) {
      left: -20px;
    }
  }
  & .swiper-button-next {
    right: -45px;
    @media (max-width: 1490px) {
      right: -20px;
    }
  }

  &__container--sleep {
    margin-bottom: 103px;
    @media (max-width: 768px) {
      // margin-bottom: 113px;
      margin-bottom: 14.71354166666667vw;
    }
    @media (max-width: 360px) {
      margin-bottom: 48px;
    }

    & .market__wrap {
      @media (max-width: 750px) {
        display: none;
      }
      @media (max-width: 360px) {
        display: none;
      }
    }
  }

  &__container--baby {
    // background-image: url(../img/cat-baby-bg.png);
    // background-repeat: no-repeat;
    // background-position: 0% 0%;

    & .catalog {

      &__caption {
        @media (max-width: 1200px) {
          margin-bottom: 86px;
        }
        @media (max-width: 768px) {
          margin-bottom: 11.19791666666667vw;
        }
        @media (max-width: 360px) {
          margin-bottom: 39px;
        }
      }
      &__title {
        margin-right: 4%;
        & span {
          position: relative;
          display: inline-block;
          padding-right: 41px;
          &::after {
            position: absolute;
            content: "";
            right: 0;
            bottom: 50%;
            transform: translateY(50%);
            width: 20px;
            height: 20px;
            background-image: url('../img/heart.svg');
            background-size:contain;
          }
        }
      }
      &__card {
        // &-img {
        //   position: static;
        //   object-fit: contain;
        //   object-position: 50% 50%;
        //   margin-bottom: 22px;
        // }
        &-wrap-img {
          position: relative;
          width: 100%;
          padding-bottom: 67.6%;
          // margin-bottom: -21px;
          @media (max-width: 768px) {
            margin-bottom: 10px;
          }
        }

        &-img {
          position: absolute;
          top: 0;
          left: 0;
          // object-fit: contain;
          // object-position: 50% 50%;
          width: 100%;
          // height: 200px;
        }

        &-caption {
          margin-bottom: 15px;
        }
      }

      &__carousel-wrap {
        margin-bottom: 60px;
        @media (max-width: 768px) {
          margin-bottom: 41px;
        }
        @media (max-width: 768px) {
          margin-bottom: 106px;
          margin-bottom: 32px;
        }
        @media (max-width: 360px) {
          margin-bottom: 106px;
          // margin-bottom: 32px;
        }
      }
    }

    & .market {
      &__link--sertificate {
        width: auto;
        @media (max-width: 1386px) {
          display: none;
        }
      }

      &__links-wrap {

        @media (max-width: 750px) {
          flex-direction: column;
          width: 100%;
        }
        @media (max-width: 360px) {
          flex-direction: column;
          width: 100%;
        }
      }

      &__link {
        @media (max-width: 750px) {
          width: auto;
          width: 100%;
          margin: 0;
          @include mb (20px);
        }
        @media (max-width: 360px) {
          width: auto;
          width: 100%;
          margin: 0;
          @include mb (20px);
        }

      }
    }



  }
}
