.advantages {
  @media (min-width:1441px) {
    margin-top: 109px;
  }
  margin-top: 7.569444444444444vw;
  margin-bottom: 61px;
  color: var(--color-light);

  @media (max-width:1200px) {
    margin-top: 109px;
  }
  @media (max-width:768px) {
    margin-top: 59px;
  }
  @media (max-width:360px) {
    margin-top: 77px;
  }

  &__container {
    max-width: 1237px;
    margin: 0 auto;
    @media (max-width:1300px) {
      padding-left: 28px;
      padding-right: 13px;
    }
    @media (max-width:1200px) {
      padding-left: 28px;
      padding-right: 13px;
    }
    @media (max-width:768px) {
      padding: 0 12px;
      padding-left: 30px;
    }
    @media (max-width:730px) {
      padding: 0 12px;
    }
    @media (max-width:360px) {
      padding: 0 12px;
    }
  }

  &__caption {
    display: flex;
    align-items: baseline;
    margin-bottom: 48px;
    @media (max-width:1180px) {
      margin-bottom: 62px;
    }
    @media (max-width:768px) {
      margin-bottom: 62px;
    }
    @media (max-width:730px) {
      margin-bottom: 0;
    }
    @media (max-width:600px) {
      align-items: center;
      margin-left: 9px;
      margin-bottom: 0;
    }
    @media (max-width:360px) {
      align-items: center;
      margin-left: 9px;
      margin-bottom: 0;
    }
  }

  &__title {
    margin-right: 31px;
    font-size: 32rem;
    font-weight: 200;
    letter-spacing: -0.2px;
    color: var(--color-light);
    @media (max-width:1200px) {
      margin-right: 15px;
    }
    @media (max-width:1000px) {
      font-size: 28rem;
    }
    @media (max-width:768px) {
      margin-right: 32px;
      font-size: 24rem;
    }
    @media (max-width:600px) {
      width: min-content;
      margin-right: 14px;
      // font-size: 14rem;
      // line-height: 1.5;
      // letter-spacing: 1.1px;
    }
    @media (max-width:450px) {
      width: min-content;
      margin-right: 14px;
      font-size: 18rem;
      line-height: 1.5;
      letter-spacing: 1.1px;
    }
    @media (max-width:360px) {
      width: min-content;
      margin-right: 14px;
      font-size: 14rem;
      line-height: 1.5;
      letter-spacing: 1.1px;
    }

    & span {
      @media (max-width:360px) {
        font-size: 16rem;
        letter-spacing: 0.8px;
      }
    }
  }

  &__logo {
    font-family: "magnolia-script";
    font-size: 40rem;
    @media (max-width:1000px) {
      font-size: 36rem;
    }
    @media (max-width:768px) {
      font-size: 32rem;
    }
    @media (max-width:600px) {
      font-size: 40rem;
    }
    @media (max-width:360px) {
      font-size: 40rem;
    }
  }

  &__inner {
    position: relative;
    display: flex;
    min-height: 476px;
    padding-right: 40px;
    padding-bottom: 29px;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    background-color: rgba(255, 255, 255, 0.1);
    @media (max-width:1200px) {
      padding-right: 28px;
    }
    @media (max-width:800px) {
      padding-right: 8px;
    }
    @media (max-width:768px) {
      padding-right: 8px;
      padding-bottom: 25px;
    }
    @media (max-width:730px) {
      flex-direction: column;
      padding-right: 0;
      background-color: transparent;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.1);
      transform: translateX(-100%);
      @media (max-width:730px) {
        display: none;
      }
    }
  }

  &__content {
    flex-grow: 1;
    padding-top: 62px;
    padding-bottom: 50px;
    padding-right: 20px;
    @media (max-width:1200px) {
      padding-top: 37px;
      // padding-left: 20px;
      padding-bottom: 16px;
    }
    @media (max-width:768px) {
      padding-top: 34px;
      padding-bottom: 16px;
    }
    @media (max-width:730px) {
      margin-bottom: 29px;
      padding-right: 40px;
    }
    @media (max-width:360px) {
      margin-bottom: 29px;
      padding-top: 41px;
    }
  }

  &__text {
    position: relative;
    @include mb (49px);
    padding-left: 59px;
    font-size: 24rem;
    @media (max-width:1250px) {
      @include mb (51px);
      font-size: 22rem;
    }
    @media (max-width:1200px) {
      @include mb (51px);
      font-size: 22rem;
    }
    @media (max-width:1160px) {
      @include mb (40px);
      font-size: 20rem;
      line-height: 1.5;
    }
    @media (max-width:1105px) {
      @include mb (35px);
      font-size: 18rem;
    }
    @media (max-width:884px) {
      @include mb (28px);
      font-size: 18rem;
    }
    @media (max-width:768px) {
      font-size: 18rem;
    }
    @media (max-width:767px) {
      font-size: 16rem;
    }
    @media (max-width:360px) {
      @include mb (25px);
      padding-left: 53px;
      line-height: 1.4;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(9%);
      display: block;
      width: 39px;
      height: 45px;
      // background-image: url(../img/arrow-right.svg);
      // background-image: url(../img/adv.svg);
      background-image: url(../img/a.svg);
      background-size: contain;
      background-repeat: no-repeat;
      @media (max-width:730px) {
        width: 30px;
        height: 35px;
      }
      @media (max-width:360px) {
        width: 24px;
        height: 27px;
        top: 4px;
        left: 8px;
        transform: translateY(0%);
      }
    }
  }

  &__video {
    position: relative;
    flex-shrink: 0;
    width: 380px;
    @media (max-width:730px) {
      width: 100%;
    }
    // height: 490px;
    // height: calc(1,289473684210526 * 380);
    // object-fit: cover;


    & .wrap {
      position: absolute;
      width: 100%;
      padding-top: 128.9473684210526%;
      bottom: 0;
      left: 0;
      @media (max-width:730px) {
        position: relative;
      }
    }

    & video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 40px;
      object-fit: cover;
      // transform: translateY(-46px);
    }
  }


}
