.materials {
  margin-top: 183px;
  margin-bottom: 91px;
  color: var(--color-light);

  &__container {
    margin: 0 auto;
    max-width: 1440px;
    padding-left: 20px;
    padding-right: 20px;
    @media (max-width: 360px) {
      padding-left: 12px;
      padding-right: 12px;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: 277px auto;
    @media (max-width: 768px) {
      grid-template-columns: 236px auto;
    }
    @media (max-width: 650px) {
      grid-template-columns: auto;
    }
  }

  &-top-wrap {
    // grid-column: 2/-1;
    // display: grid;
    // grid-template-columns: repeat(9, 11.11111111111111%);
  }

  &__grid-4-1 {
    grid-column: 2/-1;
    max-width: 806px;
    padding-left: 53px;
    @media (max-width: 768px) {
      padding-left: 38px;
    }
    @media (max-width: 650px) {
      grid-column: 1;
      padding-left: 0;
    }
  }

  &__title {
    margin-bottom: 31px;
    font-size: 36rem;
    font-weight: 600;
    @media (max-width: 750px) {
      font-size: 32rem;
    }
  }

  &__middle-title {
    font-size: 24rem;
    @media (max-width: 750px) {
      font-size: 22rem;
    }
  }

  & p, & li {
    font-size: 18rem;
    font-weight: 300;
    @media (max-width: 750px) {
      font-size: 16rem;
    }
  }

  &__base-title {
    margin-bottom: 29px;
  }

  &__list {
    padding-left: 27px;
    & li {
      max-width: 709px;
      line-height: 27px;
      &::marker {
        font-size: 15лбpx;
      }
    }
  }

  &__base-img {
    grid-column: 1/2;
    border-radius: 8%;
    @media (max-width: 650px) {
      width: 100%;
      margin-bottom: 25px;
    }
  }

  &__external-title {
    margin-top: 17px;
    margin-bottom: 28px;
  }

  &__p-wrap {
    display: grid;
    grid-column: 1/-1;
    grid-template-columns: 277px auto;
    @media (max-width: 768px) {
      grid-template-columns: 236px auto;
    }
    @media (max-width: 650px) {
      grid-template-columns: auto;
    }
    & p {
      padding-bottom: 27px;
    }
    & span {
      font-weight: 600;
    }

  }

  &__external-img {
    grid-column: 1/2;
    justify-self: center;
    max-height: 136px;
    object-fit: cover;
    @media (max-width: 650px) {
      grid-column: 1;
      grid-row:2;
      width: 100%;
      max-height: none;
    }
  }

  &__descr {
    line-height: 27px;
    &:not(:last-child) {
      margin-bottom: 27px;
    }
  }
}
